import styled from "styled-components";
import { Row, Col, Button as BS } from 'react-bootstrap';
import SVG from "react-inlinesvg";
import React, {useMemo, useState} from "react";
import { Link } from 'react-router-dom';
import {useTranslation} from "react-i18next";

import Page from "../../components/Page";
import DefaultCard from '../../components/Card';
import {
	InputGroup,
	InputGroupFormControl as FormControl,
	InputGroupPrepend,
	InputGroupText
} from "../../components/Form";
import SearchIcon from "../../assets/images/search.svg";
import CurrencyLogo from "../../components/CurrencyLogo";
import {ETHER} from "@uniswap/sdk";
import CurrencyText from "../../components/CurrencyText";
import {Table} from "./Table";
import {darken} from "polished";

const Button = styled(BS)`
  min-height: 48px;
  height: 48px;
  border-radius: 18px;
  font-weight: 500;
  font-size: 1rem;
  min-width: 105px;
  
  @media (max-width: 991px) {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 1;
    font-size: .875rem;
    min-height: 40px;
    height: 40px;
    padding: 0;
    border-radius: 12px;
  }
`

const Title = styled.h1`
	margin: 0;
	font-size: 2.5rem;
	font-weight: 700;
	color: ${({ theme }) => theme.text1};
	line-height: 3rem;

	
	@media (max-width: 1199px) {
		font-size: 2.25rem;
	}
	@media (max-width: 991px) {
		font-size: 2rem;
	  	margin-bottom: 0.5rem;
	}
	@media (max-width: 767px) {
		font-size: 1.5rem;
	}
`

const Header = styled.div`
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 24px;
	margin-top: 40px;
	
	@media(max-width: 991px) {
		margin-top: 10px;
	  	flex-direction: column;
	  	align-items: stretch;
	}
`

const Card = styled(DefaultCard)`
	& > .card-body {
		padding: 30px 30px 24px;
	  
	  	@media (max-width: 576px) {
		  	padding: 16px;
	    }
	}
`

const CellText = styled.span`
  font-size: 1rem;
  font-weight: 500;
  color: ${({ theme }) => theme.text1};
  display: block;
  line-height: 1.25rem;
  
  @media (max-width: 991px) {
    font-size: .875rem;
  }
  @media (max-width: 767px) {
    font-size: .75rem;
  }
`

const CurrencySymbol = styled(CellText)`
  margin-bottom: 5px;


  @media (max-width: 991px) {
    margin-bottom: 0;
  }
`

const CurrencyName = styled.span`
  font-size: .875rem;
  font-weight: 400;
  line-height: 1.125rem;
  color: ${({ theme }) => theme.text3};


  @media (max-width: 991px) {
    font-size: .75rem;
  }
  @media (max-width: 767px) {
    font-size: .625rem;
  }
`

const CoinContent = styled.div`
  margin-left: 45px;
  
  @media (max-width: 1199px) {
    margin-left: 24px;
  }
  
  @media (max-width: 991px) {
    margin-left: 0;
    margin-right: 24px;
  }
  
  @media (max-width: 991px) {
    margin-right: 16px;
  }
`

const ShowMoreButton = styled.button`
  background-color: transparent;
  padding: 8px 0;
  border: none;
  border-bottom: 2px solid ${({ theme }) => theme.primary};
  font-size: 1rem;
  font-weight: 500;
  line-height: 1.25rem;
  color: ${({ theme }) => theme.primary};
  display: inline-block;
  margin-top: 2rem;
  
  &:hover,
  &:focus,
  &:active {
    outline: none;
    text-decoration: none;
    box-shadow: none;
  }
  
  &:hover {
    color: ${({ theme }) => darken(0.1, theme.primary)};
    border-bottom-color: ${({ theme }) => darken(0.1, theme.primary)};
  }
`

const CrossBalance = props => {
	const { t } = useTranslation()
	const [query, setQuery] = useState('');
	const [showFull, setShowFull] = useState(false);

	const changeSearchInput = (e) => {
		setQuery(e.target.value);
	}

	const columns = useMemo(() => {
		return [
			{
				dataField: 'coin',
				text: t('cross.coin'),
				formatter: (cell, row, rowIndex) => {
					return (
						<div className="d-flex align-items-center flex-row-reverse flex-lg-row">
							<CurrencyLogo
								currency={ETHER}
								size={'32px'}
							/>
							<CoinContent className="d-flex flex-column align-items-stretch justify-content-center">
								<CurrencySymbol>{ETHER.symbol}</CurrencySymbol>
								<CurrencyName>{ETHER.name}</CurrencyName>
							</CoinContent>
						</div>
					)
				}
			},
			{
				dataField: 'price',
				text: t('cross.price'),
				formatter: (cell, row, rowIndex) => {
					return (
						<CellText>
							<CurrencyText>65448</CurrencyText>
						</CellText>
					)
				}
			},
			{
				dataField: 'balance',
				text: t('cross.balance'),
				formatter: (cell, row, rowIndex) => {
					return (
						<CellText>2.1659859 ETH</CellText>
					)
				}
			},
			{
				dataField: 'liquidity',
				text: t('cross.liquidity'),
				formatter: (cell, row, rowIndex) => {
					return (
						<CellText>0</CellText>
					)
				}
			},
			{
				dataField: 'totalBalance',
				text: t('cross.totalBalance'),
				formatter: (cell, row, rowIndex) => {
					return (
						<CellText>2.1659859 ETH</CellText>
					)
				}
			},
			{
				dataField: 'action',
				text: t('cross.action'),
				formatter: (cell, row, rowIndex) => {
					return (
						<Button as={Link} className={'btn btn-light-primary'} to={`/cross/anyswap?inputCurrency=ETHER`}>Swap</Button>
					)
				},
				isAction: true
			},
		]
	}, [])

	const data = useMemo(() => {
		let result =  [...Array(10)].map((_, i) => {
			return {
				id: _ + i
			}
		})

		if(!showFull) {
			return result.slice(0, 3);
		}

		return result;
	}, [showFull])

	return (
		<Page notNetworkSensitive={true}>
			<Row>
				<Col xs={12}>
					<Header>
						<Title>My Balances</Title>
						<InputGroup className={"w-auto"}>
							<InputGroupPrepend>
								<InputGroupText>
									<SVG src={SearchIcon} />
								</InputGroupText>
							</InputGroupPrepend>
							<FormControl
								value={query}
								id="CrossBalanceSearch"
								placeholder={t("search")}
								onChange={changeSearchInput}
							/>
						</InputGroup>
					</Header>
				</Col>
				<Col xs={12}>
					<Card>
						<div className="d-flex flex-column align-items-stretch">
							<Table
								columns={columns}
								entities={data}
							/>
							{!showFull && (
								<div className="align-self-center text-center">
									<ShowMoreButton onClick={setShowFull.bind(this, true)}>See More</ShowMoreButton>
								</div>
							)}
						</div>
					</Card>
				</Col>
			</Row>
		</Page>
	)
}

export default CrossBalance;